/* Box sizing rules */
@import url("//fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,400;0,700;1,400&display=swap");
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
* {
  margin: 0;
  padding: 0;
  font: inherit;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

html,
body {
  font-size: 16px;
}

/* Set core body defaults */
body {
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture,
svg {
  max-width: 100%;
  height: auto;
  display: block;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
.preload * {
  -webkit-transition: none !important;
  -moz-transition: none !important;
  -ms-transition: none !important;
  -o-transition: none !important;
  transition: none !important;
}

@font-face {
  font-family: "icons";
  src: url("../fonts/fontello.woff2");
  src: url("../fonts/fontello.woff2") format("woff"), url("../fonts/fontello.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
  font-display: block;
}
.icons {
  font-style: normal;
  font-family: "icons";
  font-weight: normal;
}

:root {
  --root-font-size: 16px;
  --ff-primary: "Montserrat", serif;
  --fs-100: 0.8125rem;
  --fs-200: 0.875rem;
  --fs-300: 0.9375rem;
  --fs-400: 1rem;
  --fs-500: 1.125rem;
  --fs-600: 1.25rem;
  --fs-700: 1.375rem;
  --fs-800: 1.5rem;
  --fs-900: 1.625rem;
  --fs-1000: 2em;
  --fs-1500: 3.25rem;
  --fw-thin: 100;
  --fw-regular: 400;
  --fw-medium: 500;
  --fw-semi-bold: 600;
  --fw-bold: 700;
  --fw-extra-bold: 800;
  --lh-100: 0.75;
  --lh-200: 1;
  --lh-300: 1.25;
  --lh-400: 1.5;
  --lh-500: 2;
  --c-primary: #009ffe;
  --c-primary-rgb-10: rgb(41 148 255 / 20%);
  --c-secondary: #bdd2e7;
  --c-black: #17171a;
  --c-black-alt: #0a0a0a;
  --c-white: #fefefe;
  --c-grey-100: #343434;
  --c-grey-200: #1e1e23;
  --c-grey-300: #878787;
  --c-grey-400: #bebebe;
  --c-grey-500: #dcdcdc;
  --c-gradient: #ff5733 0%, #FFC300 100%;
  --w-large: 1920px;
  --w-medium: 1680px;
  --w-default: 1440px;
  --w-small: 1240px;
  --w-tiny: 800px;
  --t-fast: 0.3s;
  --t-medium: 0.5s;
  --t-slow: 1s;
}
:root .width-lg {
  width: min(var(--w-large), 100% - 2em);
}
:root .width-md {
  width: min(var(--w-medium), 100% - 2em);
}
:root .width-df {
  width: min(var(--w-default), 100% - 2em);
}
:root .width-sm {
  width: min(var(--w-small), 100% - 2em);
}
:root .width-xs {
  width: min(var(--w-tiny), 100% - 2em);
}

.slideshow {
  display: grid;
  place-items: start;
}

.slideshow__slide {
  opacity: 0;
  z-index: -1;
  grid-column: 1/2;
  grid-row: 1/2;
  transition: all var(--t-medium);
}

.slideshow__slide[data-status=active] {
  opacity: 1;
  z-index: 1;
}

.slideshow__dot {
  width: 1em;
  height: 1em;
}

.slideshow__dot[data-status=active] {
  background-color: red;
}

html {
  scroll-behavior: smooth;
}

body {
  position: relative;
  background-color: var(--c-black);
}

.content {
  margin-inline: auto;
}

h1, h2, h3, main, body, .text-100 {
  font-family: var(--ff-primary);
  font-size: var(--fs-500);
  font-weight: var(--fw-regular);
  line-height: var(--lh-500);
  color: var(--c-white);
  text-decoration: none;
  text-transform: none;
  font-style: normal;
}

.text-200--h2--orange-yellow h2, .text-200--h1--orange-yellow h1, .text-200--h2 h2, .text-200 {
  font-family: var(--ff-primary);
  font-size: clamp(var(--fs-1000), 5vw, var(--fs-1500));
  font-weight: var(--fw-thin);
  line-height: var(--lh-400);
  color: var(--c-white);
  text-decoration: none;
  text-transform: none;
  font-style: normal;
}

.text-200--h2--orange-yellow h2, .text-200--h1--orange-yellow h1 {
  background: linear-gradient(to right, var(--c-gradient));
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
}

.post__heading, .text-300--h2--orange-yellow h2, .text-300--h2 h2, .text-300 {
  font-family: var(--ff-primary);
  font-size: clamp(var(--fs-700), 3vw, var(--fs-900));
  font-weight: var(--fw-regular);
  line-height: var(--lh-400);
  color: var(--c-white);
  text-decoration: none;
  text-transform: none;
  font-style: normal;
}

.text-300--h2--orange-yellow h2 {
  background: linear-gradient(to right, var(--c-gradient));
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
}

.header__logo h1,
.header__logo a, .text-400 {
  font-family: var(--ff-primary);
  font-size: var(--fs-1000);
  font-weight: 100;
  line-height: var(--lh-400);
  color: var(--c-white);
  text-decoration: none;
  text-transform: none;
  font-style: normal;
}

.post__date, .post__category, .text-500 {
  font-family: var(--ff-primary);
  font-size: var(--root-font-size);
  font-weight: var(--fw-regular);
  line-height: var(--lh-400);
  color: var(--c-grey-300);
  text-decoration: none;
  text-transform: none;
  font-style: normal;
}

main p {
  margin-bottom: 1em;
}
main p:last-of-type {
  margin-bottom: 0;
}

strong {
  font-weight: var(--fw-bold);
}

em {
  font-style: italic;
}

ul, ol {
  margin-left: 1em;
}

a {
  color: var(--c-primary);
  text-decoration: none;
  transition: all var(--t-medium);
}
a:hover, a:focus {
  color: var(--c-white);
  text-shadow: 1px 1px 2px var(--c-black-alt), 0 0 1em var(--c-primary), 0 0 0.2em var(--c-primary);
}

.categories__select, .category__btn, .header__navigation a, .button {
  font-family: var(--ff-primary);
  font-size: clamp(var(--fs-400), 3vw, var(--fs-500));
  font-weight: var(--fw-medium);
  line-height: var(--lh-400);
  color: var(--c-primary);
  text-decoration: none;
  text-transform: lowercase;
  font-style: normal;
  background-color: transparent;
  border: 1px solid var(--c-grey-100);
  border-radius: 0 1em 0 1em;
  padding: 0.7em 1.25em;
  transition: all var(--t-fast);
  cursor: pointer;
}
.categories__select:hover, .category__btn:hover, .header__navigation a:hover, .button:hover, .categories__select:focus, .category__btn:focus, .header__navigation a:focus, .button:focus {
  background-color: var(--c-black-alt);
  color: var(--c-white);
  box-shadow: 1px 1px 50px var(--c-primary-rgb-10);
  text-shadow: none;
}

.icon:before {
  content: "";
  font-family: "icons";
  font-size: var(--fs-500);
  color: var(--c-primary);
}

.icon--address:before {
  content: "\e814";
}

.icon--phone:before {
  content: "\e815";
}

.social {
  list-style: none;
  margin: 0;
}
.social a:before {
  content: "\e800";
  font-family: "icons";
}

.social__instagram a:before {
  content: "\f16d";
}

.social__linkedin a:before {
  content: "\e809";
}

.social__lastfm a:before {
  content: "\f202";
}

.social__github a:before {
  content: "\f09b";
}

.social__codepen a:before {
  content: "\f32c";
}

.error {
  position: fixed;
  inset: 0 auto auto 0;
  width: 100%;
  padding-inline: 1em;
  background-color: #9d0000;
  color: var(--c-white);
  text-align: center;
  z-index: 99999;
  transition: all var(--t-fast);
  opacity: 0;
}
.error.active {
  opacity: 1;
}

.header__container {
  display: flex;
  justify-content: space-between;
}

.header__logo h1,
.header__logo a {
  transition: all 1.75s;
}
.header__logo h1 span,
.header__logo a span {
  display: inline-block;
  max-width: 0;
  opacity: 0;
  transition: all 1.75s;
}
.header__logo h1:hover, .header__logo h1:focus,
.header__logo a:hover,
.header__logo a:focus {
  cursor: default;
}
.header__logo h1:hover span, .header__logo h1:focus span,
.header__logo a:hover span,
.header__logo a:focus span {
  max-width: 100%;
  opacity: 1;
}
.header__logo a:hover, .header__logo a:focus {
  cursor: pointer;
  color: var(--c-primary);
}

.header__navigation {
  display: flex;
  align-items: center;
}
.header__navigation ul {
  display: flex;
  gap: 1.5em;
  margin: 0;
  list-style: none;
}
.footer__container {
  display: flex;
  justify-content: space-between;
}
@media (max-width: 480px) {
  .footer__container {
    flex-direction: column;
    align-items: center;
  }
}

.footer__copyright {
  display: flex;
  align-items: center;
}

.footer__social ul {
  display: flex;
  gap: clamp(1rem, 2vw, 1.5rem);
}
.footer__social a {
  font-size: clamp(1.5rem, 2vw, 2rem);
  color: var(--c-white);
}
.footer__social a:hover, .footer__social a:focus {
  color: var(--c-primary);
  text-shadow: 1px 1px 50px var(--c-primary);
}

.about__container {
  display: grid;
  gap: 3em;
  grid-template-columns: 3fr 1fr;
}
@media (max-width: 1120px) {
  .about__container {
    grid-template-columns: 1fr;
  }
}

.about__item {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.about__item--text h2 {
  margin-bottom: 0.5em;
}

.about__item--image {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.about__item--image .slideshow {
  width: 348px;
}
@media (max-width: 1120px) {
  .about__item--image .slideshow {
    width: 100%;
    grid-template-columns: repeat(3, 1fr);
    gap: clamp(0.25em, 2vw, 1em);
  }
  .about__item--image .slideshow .slideshow__slide--vw600 {
    opacity: 1;
    z-index: 1;
    grid-column: auto;
    grid-row: auto;
  }
  .about__item--image .slideshow .slideshow__slide--vw600:nth-of-type(n+4) {
    display: none;
  }
  .about__item--image .slideshow img {
    width: 348px;
  }
}
@media (max-width: 480px) {
  .about__item--image .slideshow {
    max-width: 348px;
    grid-template-columns: 1fr;
    margin-inline: auto;
  }
}
.about__item--image img {
  border-radius: 100%;
  border: clamp(0.25em, 2vw, 1em) solid var(--c-grey-100);
}

.blog__intro h2 {
  margin-bottom: 1em;
}

.blog--home {
  text-align: center;
}

.categories__container {
  display: flex;
  justify-content: center;
  gap: 1em;
  flex-wrap: wrap;
}

.category__btn {
  flex-grow: 1;
  flex-shrink: 1;
}
@media (max-width: 1024px) {
  .category__btn {
    flex-grow: 1;
    flex-shrink: 1;
    flex-basis: 20vw;
    max-width: 150px;
  }
}
.category__btn.active {
  background-color: var(--c-black-alt);
  color: var(--c-white);
  box-shadow: 1px 1px 50px var(--c-primary-rgb-10);
  text-shadow: none;
}
@media (max-width: 1024px) {
  .category__btn {
    display: none;
  }
}

.categories__select {
  box-shadow: 1px 1px 50px var(--c-primary-rgb-10);
  display: none;
}
@media (max-width: 1024px) {
  .categories__select {
    display: block;
  }
}

.blog__posts {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 3em;
}
@media (max-width: 1024px) {
  .blog__posts {
    grid-template-columns: 1fr;
  }
}

.post__item {
  opacity: 0;
  transition: opacity 0.25s;
  display: none;
  flex-direction: column;
  justify-content: center;
  position: relative;
  padding: 5em 3em;
  background-color: var(--c-grey-200);
  border-radius: 1em;
}
.post__item.fade {
  opacity: 1;
}
.post__item.active {
  display: flex;
}
.post__item:only-child {
  grid-column: 1/-1;
  margin-inline: auto;
  width: 50%;
}
@media (max-width: 1024px) {
  .post__item:only-child {
    width: 100%;
  }
}

.post__heading {
  order: 2;
  margin-bottom: 0.25em;
}
.post__heading a {
  display: inline-block;
}

.post__category {
  inset: 2em 50% auto auto;
  order: 1;
}

.post__date {
  inset: auto 50% 2em auto;
  order: 3;
}

.post__heading,
.post__category,
.post__date {
  text-align: center;
}

.post__category,
.post__date {
  position: absolute;
  transform: translate(50%, 0);
}

.blog__more {
  width: min(300px, 100%);
  margin-inline: auto;
  text-align: center;
}

#skip-nav {
  position: absolute;
  top: 0;
  width: 300px;
}
#skip-nav a:link,
#skip-nav a:visited {
  background-color: #fff;
  color: #000;
  font-size: 20px;
  font-weight: 400;
  overflow: hidden;
  padding: 10px;
  position: absolute;
  left: -9999px;
  text-decoration: none;
  width: 1px;
  height: 1px;
}
#skip-nav a:focus {
  left: 0;
  width: auto;
  height: auto;
  outline: 1px solid #000;
  overflow: visible;
  text-decoration: none;
  z-index: 20000;
}
#skip-nav a:hover {
  background-color: #000;
  color: #fff;
}

.site-map {
  margin: 0;
  padding: 0;
  list-style: none;
}
.site-map li {
  margin: 0 0 0.5em 0;
}
.site-map li a.not-a-link {
  display: none;
}
.site-map li ul {
  list-style: none;
  margin: 0.5em 0 0 0;
  padding: 0;
}

.footer__container {
  border-top: 1px solid var(--c-grey-100);
}

.header__container, .about {
  border-bottom: 1px solid var(--c-grey-100);
}

.about, .blog--archive, .blog__categories, .blog__posts, .blog__more {
  margin-block: clamp(3em, 5vw, 5em);
}

.header__container, .footer__container {
  padding-block: clamp(1em, 5vw, 3em);
}

.about {
  padding-bottom: clamp(3em, 5vw, 5em);
}