// ***********************************
//  Import Fonts
// ***********************************
@import url('//fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,400;0,700;1,400&display=swap');




// ***********************************
//  Icon font via Fontello.com
//  To add / remove / edit icons within this font, visit www.fontello.com and click the 'wrench' icon at the top-right. Then select "Import" and upload either the zip file or config.json file found within the 'fonts' directory within this website.
// ***********************************
@font-face {
	font-family: 'icons';
	src: url('../fonts/fontello.woff2');
	src: url('../fonts/fontello.woff2') format('woff'), url('../fonts/fontello.ttf') format('truetype');
	font-weight: normal;
	font-style: normal;
	font-display: block;
}

.icons {
	font-style: normal;
	font-family: 'icons';
	font-weight: normal;
}